@import url(https://fonts.googleapis.com/css?family=Roboto:300);
body {
  background: #76b852; /* fallback for old browsers */
  background: -webkit-gradient(linear, right top, left top, from(#76b852), to(#8DC26F));
  background: linear-gradient(to left, #76b852, #8DC26F);
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
}

button {
  cursor: pointer;
}
table {
  margin-left: 10px;
  margin-right: 10px;
}

button.block {
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  outline: 0;
  background: #4CAF50;
  -webkit-transition: background 300ms ease-in-out;
  transition: background 300ms ease-in-out;
  width: 100%;
  border: 0;
  padding: 15px;
  color: #FFFFFF;
  font-size: 18px;
}

button.block:hover, button.block:focus {
  background: #43A047;
  -webkit-transition: background 300ms ease-in-out;
  transition: background 300ms ease-in-out;
}

button.block:active {
  background: #56cc5a;
  -webkit-transition: background 50ms ease-in-out;
  transition: background 50ms ease-in-out;
}

.enter-page {
    width: 90%;
    padding: 8% 0 0;
    margin: auto;
    text-align: center;
}

.enter-main-region {
    position: relative;
    z-index: 1;
    background: #fff;
    max-width: 360px;
    margin: 0 auto 100px;
    padding: 45px;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}

/*
=====
LEVEL 1. CORE STYLES
=====
*/

.toggle{
    --uiToggleSize: var(--toggleSize, 20px);
    --uiToggleIndent: var(--toggleIndent, 1em);
    --uiToggleBorderWidth: var(--toggleBorderWidth, 1px);
    --uiToggleColor: var(--toggleColor, #000);
    /*--uiToggleDisabledColor: var(--toggleDisabledColor, #868e96);*/
    --uiToggleBgColor: var(--toggleBgColor, #fff);
    --uiToggleArrowWidth: var(--toggleArrowWidth, 2px);
    --uiToggleArrowColor: var(--toggleArrowColor, #fff);

    display: inline-block;
    position: relative;
    margin: 30px 0 0;
}

.toggle__input{
    position: absolute;
    left: -99999px;
}

.toggle__label{
    color: var(--uiToggleColor);
    display: inline-flex;
    cursor: pointer;
    min-height: var(--uiToggleSize);
    padding-left: calc(var(--uiToggleSize) + var(--uiToggleIndent));
}

.toggle__label:before, .toggle__label:after{
    content: "";
    box-sizing: border-box;
    width: 1em;
    height: 1em;
    font-size: var(--uiToggleSize);

    position: absolute;
    left: 0;
    top: 0;
}

.toggle__label:before{
    border: var(--uiToggleBorderWidth) solid var(--uiToggleColor);
    z-index: 2;
}

/*.toggle__input:disabled ~ .toggle__label:before{*/
/*    border-color: var(--uiToggleDisabledColor);*/
/*}*/

.toggle__input:focus ~ .toggle__label:before{
    box-shadow: 0 0 0 1px var(--uiToggleBgColor), 0 0 0 2px var(--uiToggleColor);
}

.toggle__input:not(:disabled):checked:focus ~ .toggle__label:after{
    box-shadow: 0 0 0 1px var(--uiToggleBgColor), 0 0 0 2px var(--uiToggleColor);
}

.toggle__input:not(:disabled) ~ .toggle__label:after{
    background-color: var(--uiToggleColor);
    opacity: 0;
}

.toggle__input:not(:disabled):checked ~ .toggle__label:after{
    opacity: 1;
}

.toggle__text{
    margin-top: auto;
    margin-bottom: auto;
}

/*
The arrow size and position depends from sizes of square because I needed an arrow correct positioning from the top left corner of the element toggle
*/

.toggle__text:before{
    content: "";
    box-sizing: border-box;
    width: 0;
    height: 0;
    font-size: var(--uiToggleSize);

    border-left-width: 0;
    border-bottom-width: 0;
    border-left-style: solid;
    border-bottom-style: solid;
    border-color: var(--uiToggleArrowColor);

    position: absolute;
    top: .5428em;
    left: .2em;
    z-index: 3;

    -webkit-transform-origin: left top;

            transform-origin: left top;
    -webkit-transform: rotate(-40deg) skew(10deg);
            transform: rotate(-40deg) skew(10deg);
}

.toggle__input:not(:disabled):checked ~ .toggle__label .toggle__text:before{
    width: .5em;
    height: .25em;
    border-left-width: var(--uiToggleArrowWidth);
    border-bottom-width: var(--uiToggleArrowWidth);
    will-change: width, height;
    -webkit-transition: width .1s ease-out .2s, height .2s ease-out;
    transition: width .1s ease-out .2s, height .2s ease-out;
}

/*
=====
LEVEL 2. PRESENTATION STYLES
=====
*/

/*
The demo skin
*/

.toggle__label:before, .toggle__label:after{
    border-radius: 2px;
}

/*
The animation of switching states
*/

.toggle__input:not(:disabled) ~ .toggle__label:before,
.toggle__input:not(:disabled) ~ .toggle__label:after{
    opacity: 1;
    -webkit-transform-origin: center center;
            transform-origin: center center;
    will-change: transform;
    -webkit-transition: -webkit-transform .2s ease-out;
    transition: -webkit-transform .2s ease-out;
    transition: transform .2s ease-out;
    transition: transform .2s ease-out, -webkit-transform .2s ease-out;
}

.toggle__input:not(:disabled) ~ .toggle__label:before{
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
    -webkit-transition-delay: .2s;
            transition-delay: .2s;
}

.toggle__input:not(:disabled) ~ .toggle__label:after{
    -webkit-transform: rotateY(90deg);
            transform: rotateY(90deg);
}

.toggle__input:not(:disabled):checked ~ .toggle__label:before{
    -webkit-transform: rotateY(-90deg);
            transform: rotateY(-90deg);
    -webkit-transition-delay: 0s;
            transition-delay: 0s;
}

.toggle__input:not(:disabled):checked ~ .toggle__label:after{
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
    -webkit-transition-delay: .2s;
            transition-delay: .2s;
}

.toggle__text:before{
    opacity: 0;
}

.toggle__input:not(:disabled):checked ~ .toggle__label .toggle__text:before{
    opacity: 1;
    -webkit-transition: opacity .1s ease-out .3s, width .1s ease-out .5s, height .2s ease-out .3s;
    transition: opacity .1s ease-out .3s, width .1s ease-out .5s, height .2s ease-out .3s;
}

/*
=====
LEVEL 3. SETTINGS
=====
*/

.toggle{
    --toggleColor: #538039; /*#690e90;*/
    --toggleBgColor: #b2f28d; /*#9b59b6;*/
    --toggleSize: 1.5em;
    --toggleIndent: 0.8em;
    --toggleBorderWidth: 2px;
    --toggleArrowWidth: 2px;
    --toggleArrowColor: #fff;
}

:root {
    --add-fact-button-height: 4em;
}

#chuck-fact-manager {
    width: 90%;
    height: 500px;
    margin: auto;
}

#chuck-fact-manager-main-region {
    position: relative;
    background: #fff;
    max-width: 1000px;
    min-height: 100vh;
    padding-top: calc(4em * 1.3);
    padding-top: calc(var(--add-fact-button-height) * 1.3);
    box-sizing: border-box; /*to include the padding in the total size of the div (otherwise, min-height: 100vh doesn't work as expected)*/
    -webkit-box-sizing: border-box;
    -moz-box-sizing:    border-box;
    margin: 0 auto; /*top and bottom margins are 0, left and right are auto (the result is that the div gets centered within its parent) */
    box-shadow: 0 0 10px 3px rgba(0,0,0,0.8);
}

#new-fact-region {
    max-width: 1000px;
    width: 90%;
    position: fixed;
    top: 0;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
}

#add-fact-button {
    height: 4em;
    height: var(--add-fact-button-height);
}

.textCell {
    padding: 8px 0 8px 10px;
}

.textCell button {
    background: none;
    border: none;
    outline: none;
    padding: 0;
    text-align: left;
    /*I need to explicitly tell the button to inherit font-family and size, otherwise it will adopt
    the browser default button styles*/
    font-family: inherit;
    font-size: inherit;
}

button.icon {
    --size: 32px;
    background-color: transparent;
    outline: 0;
    border: 0;
    background-size: var(--size);
    height: var(--size);
    width: var(--size);
    /*padding: 0px;*/
    margin: 5px;
    vertical-align: middle;
}

button.unstarred {
    background-image: url(data:image/png;base64,);
}

button.unstarred:disabled {
    background-image: url(data:image/png;base64,);
    /*cursor: not-allowed;*/
    pointer-events: none;
}

button.unstarred:hover {
    background-image: url(data:image/png;base64,);
}

button.starred {
    background-image: url(data:image/png;base64,);
}

button.delete {
    background-image: url(data:image/png;base64,);
}

button.delete:disabled {
    background-image: url(data:image/png;base64,);
    pointer-events: none;
}

button.delete:hover {
    background-image: url(data:image/png;base64,);
}

.toast-button {
    background-color: #ffee70;
    color: #000;
    border: none;
    outline: none;
    cursor: pointer;
    width: 200px;
    vertical-align: middle;

    overflow: hidden;
    position: relative;
    border-radius: 0.4rem;
    display: block;
    padding: 1rem;
    box-shadow: rgba(52, 58, 64, 0.15) 0 1px 10px 0,
    rgba(52, 58, 64, 0.1) 0 6px 12px 0,
    rgba(52, 58, 64, 0.12) 0 6px 15px -2px;

}

#undo {
    vertical-align: middle;
    width: 2.5em;
    margin-right: 1.5em;
}

.Toaster__alert {
    background-color: #ff7b8e;
    overflow: hidden;
    max-width: 650px;
    position: relative;
    border-radius: 0.4rem;
    display: flex;
    padding: 1rem;
    padding-right: 48px;
    box-shadow: rgba(52, 58, 64, 0.15) 0 1px 10px 0,
    rgba(52, 58, 64, 0.1) 0 6px 12px 0,
    rgba(52, 58, 64, 0.12) 0 6px 15px -2px;
}

.Toaster__alert_text {
    box-sizing: border-box;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
    color: rgb(33, 37, 41);
    -webkit-font-smoothing: antialiased;
    font-weight: 500;
    line-height: 1.5;
    font-size: 1rem;
    margin: 0;
}

.Toaster__alert_close {
    padding: 12px;
    outline: none;
    cursor: pointer;
    background-color: transparent;
    position: absolute;
    top: 7px;
    right: 4px;
    border-radius: 0.4rem;
    border: 0;
    -webkit-appearance: none;
    font-size: 1rem;
    font-weight: 700;
    line-height: 1;
    text-shadow: 0 1px 0 #fff;
    opacity: 0.5;
}

.Toaster__alert_close:focus {
    box-shadow: rgba(52, 58, 64, 0.15) 0 0 0 3px;
}

.Toaster__message-wrapper {
    padding: 8px;
}

